$accordion-section-hover: $WildSand;

.o-accordion {
    display: flex;
    flex-direction: column;

	&__section {
        border-bottom: 2px solid lighten($FrenchGray, 20%);

        &:last-child {
            border-bottom: 0;
        }
	}
}

.m-revealer {
	&__trigger {
		display: flex;
        justify-content: space-between;
		align-items: center;

		&:hover {
			cursor: pointer;
			background: $accordion-section-hover;
		}
		.-open > & {
			&:hover {
				background: $White;
			}
		}
	}

	&__heading {
        display: flex;
        align-items: center;
        padding: $lg;
        font-size: $fs-14;
        font-weight: $regular;
        .-hopi {
            margin-right: $sm;
        }
	}
	
	&__icon {
        position: relative;
        padding: $lg;
        font-size: $fs-20;

		&::before {
			content: $bo-plus;
			.-open > * > & {
				content: $bo-minus;
				font-size: $fs-14;
			}
		}
	}
	
	&__content {
        flex-direction: column;
        justify-content: center;
		padding: 0 $lg $md $lg;
        display: none;
        
		.-open > & {
            display: flex;
            border-top: 0;
		}
	}
}
