//Coupon Code

.o-campaignBox {
    color: $Black;
    display: flex;
    flex-direction: column;
    background: $White;
    border-radius: 6px;
    box-shadow: 0px 5px 70px -20px rgba(0, 0, 0, 0.3);
    overflow: hidden;
    margin: $lg 0;
  }
  
  .o-campign-form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #000;
    padding: 5px;
    border-radius: 5px;
  
    &__input {
      padding: 10px;
      font-size: $fs-13;
    }
  }
  