.o-simpleHeader {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background: $White;
	padding: $lg;
	height: 50px;
	z-index: 1;
	position: relative;
	box-shadow: 0 0px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 7px 0 rgba(0, 0, 0, 0.12),
		0 0px 7px -1px rgba(0, 0, 0, 0.2);
	.a-headerLogo {
	}
	.a-headerFuncBtn {
		font-weight: 700;

		&::before {
			margin-right: $xs;
			font-weight: 700;
		}
	}
	.a-callCenter {
		font-size: 26px;
		color: $Emperor;
  }
  .a-secureLogo {
    height: 30px;
  }
}
[v-cloak] {
  display: none;
}
.o-loadingLarge {
  position: absolute;
  background: $White;
  opacity: 0.9;
  width: 100%;
  height: 100%;
  z-index: 2;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 20px;

  &__img {
    margin-bottom: $md;
  }
}