$order-summary-bg-color: $WildSand;
$order-summary-item-border-color: $Porcelain;
$order-summary-total-color: $Niagara;

.o-orderSummary {
	padding: $md;
	margin: $xxl $md;
	@include border-radius(5px);
	background-color: $order-summary-bg-color;

	&__item {
    display: flex;
    justify-content: space-between;
    padding: $sm;
		border-bottom: 1px solid $order-summary-item-border-color;
		
		&:last-child{
			border-bottom: 0;
		}
	}

	&__total {
		color: $order-summary-total-color;
	}
}