.o-alertModal {
	display: flex;
	border-radius: 5px;

    &__status {
        border-radius: 100%;
        width: 60px;
        height: 60px;
        margin-right: $lg;
        text-align: center;
    }
	&__content {
        display: flex;
        flex-flow: wrap;
		&--title {
			font-size: $fs-20;
            font-weight: $bold;
            margin-bottom: $md;
            width: 100%;
		}
		&--message {
            width: 100%;
			font-size: $fs-16;
			font-weight: $regular;
        }
        &--button {
        }
	}

	.-success {
        font-family: $fonticon;
		background: $Niagara;
        color: $White;
        padding-top: 18px;
        &:before {
            content: $bo-check;
            font-size: 26px;
        }
	}
	.-error {
		background: $Monza;
        color: $White;
        &:before {
            content: "!";
            font-size: 53px;
        }
	}
	.-warning {
		background: #ffc107;
		color: $Black;
	}
	.-info {
		background: $DodgerBlue;
		color: $DodgerBlue;
	}
}
