$Avenir: "Avenir",
"-apple-system",
"BlinkMacSystemFont",
"Segoe UI",
"Roboto",
"Oxygen",
"Ubuntu",
"Cantarell",
"Open Sans",
"Helvetica Neue",
sans-serif;
$fonticon: "boyner-mobile";
$base-font-size: 16px !default;

// Pixels to ems function
@function px2em($px) {
  @return ($px / $base-font-size) * 1em;
}

// Font size and line height mixin
@mixin font-size($font-size, $line-height: false) {
  font-size: px2em($font-size); /* #{$font-size} */
  @if $line-height != false {
    line-height: ($line-height / $font-size); /* #{$line-height} */
  }
}
// @include font-size(16px, 32px);
