@import "variables";

@function unicode($str) {
  @return unquote("\"") + $str + unquote("\"");
}

.bo-search {
  &:before {
    content: unicode($bo-search);
  }
}
.bo-arrow-back {
  &:before {
    content: unicode($bo-arrow-back);
  }
}
