.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  // display: inline-block;
  outline: none;
  font-weight: $regular;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  text-decoration: none;
  user-select: none;
  border: 1px solid transparent;

  line-height: 1.5;
  transition: background 0.3s ease;
  border-radius: 5px;
  cursor: pointer;

  &:active {
    opacity: 0.8;
	}
	&:focus {
		outline: 2px dashed rgba(#666, 0.5);
	}
  &.-xsmall {
    padding: 7px $sm;
    font-size: $fs-13;
  }
  &.-small {
    padding: $sm $md;
    font-size: $fs-14;
  }
  &.-medium {
    padding: 7px $lg;
    font-size: $fs-16;
  }
  &.-large {
    padding: $lg $xl;
    font-size: $fs-20;
    font-weight: $bold;
  }
  &.-black {
    background-color: $Shark;
    color: $White;
    &:active {
      background-color: lighten($Shark, 10%);
    }
  }
  &.-border {
    border-color: $Shark;
    &:active {
      background: $Niagara;
      color: $White;
      border-color: $Niagara;
    }
  }
  &.-greenBorder {
    border-color: $Niagara;
    color: $Niagara;
  }
  &.-link {
    text-decoration: underline;
  }
  &.-white {
    background-color: $White;
    color: $Shark;
  }
  &.-greenBtn {
    background: $Niagara;
    color: $White;
    position: relative;
  }
  &.-red {
    background: $Monza;
    color: $White;
  }
  &.-guestLogin {
    height: 50px;
    font-weight: $semi-bold;
  }
  &.-facebook {
    border: solid 1px $Chambray;
    color: $Chambray;
    position: relative;
    height: 50px;

    &:before {
      font-family: "boyner-mobile" !important;
      content: $bo-facebook;
      font-size: 22px;
      position: absolute;
      left: 5%;
      top: calc(50% - 15px);
    }
  }
  &.-twitter {
    border: solid 1px $DodgerBlue;
    color: $DodgerBlue;
    position: relative;
    height: 50px;

    &:before {
      font-family: "boyner-mobile" !important;
      content: $bo-twitter;
      font-size: 22px;
      position: absolute;
      left: 5%;
      top: calc(50% - 15px);
    }
  }
  &.-arrow {
    // position: relative;
    // z-index: z('page-wrapper');
    &:after {
      font-family: "boyner-mobile" !important;
      content: $bo-arrow-right;
      position: absolute;
      right: 5%;
      top: calc(50% - 16px);
    }
  }

  &.-back {
    position: relative;
    padding-left: 25px !important;
    font-weight: $bold;
    &:before {
      font-family: "boyner-mobile" !important;
      content: $bo-arrow-left;
      position: absolute;
      left: 0;
      top: calc(50% - 12px);
      font-size: $fs-16;
      
    }
  }

  &.-rounded {
    border-radius: 100px;
  }
}
