.o-productList {
  &__header {
    display: flex;
    .a-backToShopping {
      color: $FlushOrange;
      font-size: $fs-14;
      i {
        margin-right: $sm;
      }
      &:hover {
          color: $Shark;
      }
    }
    &--title {
      margin-left: 15%;
      font-weight: $regular;
      font-size: $fs-22;
      color: $Emperor;
    }
  }
  &__content {
    margin: 30px -10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
  }
}

.o-productCard {
  $this: &;
  width: calc(20% - 20px);
  background: $Gallery;
  margin: 10px;
  border-radius: 5px;
  position: relative;

  &:hover {
    background: $White;
    box-shadow: 0px 5px 70px -20px rgba(0, 0, 0, 0.3);
    .btn-addToCard {
      background: $Niagara;
      color: $White;
      border-color: $Niagara;
    }
    .btn-addToFavorite {
      display: inline-block;
    }
  }

  &__img {
  }
  &__content {
    display: flex;
    flex-direction: column;
    padding: $md;

    &--name {
      font-size: $fs-14;
      margin-bottom: $md;
    }
  }

  &__link {
    display: block;
    &:hover {
      img {
        opacity: 0.7;
      }
    }
  }
}

.m-productPrice {
  display: flex;
  align-items: center;

  &__content {
    display: flex;
    flex-direction: column;
  }
}

.btn-addToFavorite {
  width: 34px;
  height: 34px;
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: $fs-18;
  text-align: center;
  display: none;
  cursor: pointer;

  &:before {
    margin-top: 3px;
    display: inline-block;
  }
}

.btn-addToCard {
  margin-top: $md;
}

.-discount {
  background: $discount-color;
  color: $White;
  font-size: $fs-14;
  font-weight: $regular;
  border-radius: 4px;
  padding: $xs $sm;
  text-align: center;
  margin-right: $sm;

  span {
    font-weight: $regular;
    font-size: $fs-10;
    display: block;
  }
}
.-oldPrice {
  color: $oldPrice-color;
  font-size: $fs-16;
  text-decoration: line-through;
  font-weight: $regular;
}
.-salePrice {
  font-size: $fs-16;
  color: $salePrice-color;
  font-weight: $bold;
}
