$simpleSubTotal-bg-color: $Niagara;
$simpleSubTotal-item-color: $White;
$simpleSubTotal-item-btn-color: $White;

.o-simpleSubTotal {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: $md;
	background: $simpleSubTotal-bg-color;

	&__item {
		color: $simpleSubTotal-item-color;

		&--btn {
			flex-direction: row-reverse;
			color: $simpleSubTotal-item-btn-color !important;
			font-weight: $bold;
			text-transform: uppercase;

			&::before {
				margin-left: $sm;
			}
		}
	}
}