$basket-item-border: $Porcelain;
$basket-delete-btn: $DustyGray;

body {
	background: $background-color;
}

.o-basket {
	color: $Black;
	background: $White;

	margin-bottom: 40px;

	&__title {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		h1 {
			font-size: $fs-20;
		}
	}
	&__item {
		display: flex;
		flex-direction: row;
		padding: $md $md;
		margin: $md 0;

		&:last-child {
			border-bottom: none;
		}
		&:nth-child(even) {
			background-color: $background-color;
		}
	}
	&__productImg {
		// overflow: hidden;
		max-height: 105px;

		img {
			width: 76px;
			border: 1px solid $basket-item-border;
			@include border-radius(5px);
		}
	}
	&__content {
		margin-left: $sm;
		width: 100%;
		padding-right: $md;
	}

	&__productDesc {
		// width: 85%;
		&--name {
			margin-bottom: $md;
			font-weight: $bold;
			font-size: $fs-14;
		}
	}
	&__variation {
		display: flex;
		align-items: center;
		margin-bottom: $xs;

		&--item {
			font-size: $fs-11;
			color: $Shark;
		}
		&--label {
			font-weight: $bold;
			&:after {
				content: ":";
				position: absolute;
				right: 0;
				top: 0;
			}
		}
		&--val {
			margin-left: $xs;
		}
	}
	&__productInfo {
		display: flex;
		justify-content: space-between;
		border-top: solid 1px $basket-item-border;
		padding-top: $md;
		margin-top: $md;
	}
	&__productPrice {
		.-salePrice {
			font-size: 24px;
			font-weight: $bold;
		}
	}
	&__productBtn {
		color: $basket-delete-btn;
		cursor: pointer;
		font-size: $fs-14;
		position: absolute;
		right: $md;
	}
	&__productAction {
		display: flex;
		flex-direction: row;
		flex-basis: 100%;
		flex-grow: 1;
		flex-shrink: 0;
		justify-content: flex-start;
		align-items: center;
		&--item {
			margin-right: $xxl;
			&:before {
				margin-right: $xs;
				font-size: 20px;
				text-decoration: none;
			}
		}
	}
}
